.main-contact{
    background: #83a4d4;
    background: -webkit-linear-gradient(to right, #b6fbff, #83a4d4);
    background: linear-gradient(to right, #b6fbff, #83a4d4);
    
   
  }
  .contact h1{
    position:relative;
    top:60px;
    left: 37%;
    transform: translateX(-25%);
    padding: 0%;
   
  }
  .grid-container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    
    transform: translateX(12%);
    
  }
  .grid-container .contact-1{
    position: relative;
    top:150px;
  }
  .contact-1 p{
    text-overflow: none;
    max-width: 500px;
    font-size: 18px;
  }
  .contact-1 form{
    height: 80px;
    width: 600px;
  }
  .contact-1 input{
   display: inline-block;
   font-size: 18px;
   box-sizing: border-box;
  
  }
  .contact-1 input[type="text"]{
    background: #fff;
    width: 320px;
    height:40px;
    border: none;
    outline:none;
    padding:0 25px;
    border-radius: 15px 0 0 15px;
  }
  .contact-1 input[type="submit"]{
    background-color: #FFD700;
    border: none;
    outline:none;
    padding:0 25px;
    border-radius: 0 15px 15px 0;
    width: 130px;
    height:40px;
  }
  .contact-item{
      margin:18px;
  }
  .contact-item span{
    padding: 10px;
    font-size: 17px;
  }