*{
    margin: 0;
    padding: 0;
    text-decoration: none;
}
.aboutimg{
    position: relative;
    
}
hr{
    border: solid black;
}
.about-logo{
    position: absolute;
    z-index: 1;
    font-size: 7rem;
    font-weight: 700;
    color:rgb(55, 55, 55);
    top:7.5rem;
    left:40%; 

}
.list-about{
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-left: 12rem;
}
.list-item{
    margin-right: 8rem;
    text-decoration: none;
    color: #36454F;
}
.list-item:hover{
    font-size: 1.8rem;
    transition: .3s ease-in;
}
.aboutlgo{
    color: #36454F;
    margin: auto;
    font-size: 10rem;
    text-transform: uppercase;
    cursor: pointer;
  
}
.aboutlgo:hover{
    font-size: 12rem;
    font-weight: 800;
    color:darkslategrey
}

.content{
   
    word-spacing: 7px;
    
}
.whole-content{
    width: 900px;
    margin: 2rem;
    font-size: 1.5rem;
    color: grey;
}
.text-inner-div {
    position: absolute;
    top:85rem;
    transform: rotate(90deg);
    font-size: 3rem;
    color: #1c1b1b; 
}

.p-inner-div{
    position: absolute;
    width: 800px;
    color: #36454F;
    margin: 2rem;
    font-size: 1.2rem;
    top:73rem;
    left:20rem;
}
.logo-div2{
    color: #36454F;
    margin: auto;
    font-size: 5rem;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 600;
    
}
.logo-div2:hover{
    font-size: 6rem;
    font-weight: 800;
    color:darkslategrey;
}
.content-div2{
    width: 900px;
    margin: 2rem;
    font-size: 1rem;
    color: grey;
    word-spacing: 7px;
}
@media screen and (max-width:992px){
    .about-logo{
        font-size: 3rem;
        margin: 0;
        position: absolute;
        top:7rem;
        left: 4.5rem;
    }
    .aboutimg{
        width:1200px
    }
    .list-about{
        display:none;
    }
    hr{
        display: none;
    }
    .aboutcharity{
        display: flex;
        flex-direction: column;
    }
    .aboutlgo{
        font-size: 3rem;
        margin: 0.7rem;
    }
    .whole-content{
        font-size: 0.7rem;
        width: 300px;
    }
    .whoarewe{
        display: none;
    }
    .div2{
        display: flex;
        flex-direction: column;
    }
    .logo-div2{
        font-size: 2rem;
    }
    .content-div2{
        font-size: 0.7rem;
        width: 300px;
    }
    
}