.footer{
    background-color: #198754;
}
.footer-1{
    margin: 2rem 2rem 2rem 23rem;

}
.footer-2{
 margin: 2rem 2rem 2rem 23rem;
}
.input{
    border: none;
    background-color: #198754;
    border-bottom: 1px solid white;
    text-decoration: none;
    box-sizing: none;
    box-shadow: none;
}
.input::placeholder{
    color: white;
    
}

@media screen and (max-width:992px){
    .footer{
        display: flex;
        flex-direction: column;
        
    }
    .footer-1 {
        
        margin: 2rem 0 0 0;
    }
    .footer-2 {
        
        margin: 0 0 2rem 0;
    }

}