.news{
    position: absolute;
    z-index: 1;
    top:118rem;
}
.news-subhead{
    width:580px
}
@media screen and (max-width:992px){
    .news-logo{
        position: absolute;
        top:0;
        left:20%;
    }
    .news-img{
        width: 1500px ;
        height: 1200px;
    }
    .news{
        margin: 0;
        position: absolute;
        top: 93rem;
        
    }
    .content-news{
        display: flex;
        flex-direction: column;
        gap:1rem;
        width:400px;
    }
    .news-subhead{
        width:300px;
    }
}