.img-about{
    width: 45rem;
    
}

.about-container{
    display: flex;
    margin: 10rem 15rem 5rem 20rem;
    padding: 2rem;
    background-color:#ffff;
    box-shadow: rgba(28, 89, 64, 0.35) 0px 5px 15px;
}
@media screen and (max-width:992px){
    .about-container{
        flex-direction: column;
        margin: 0;
        box-shadow: none;
        padding: 0;
    }
    .img-about{
        width:20rem;
        margin: 0;
    }
    .box{
        display: none;
    }
}