*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    
}
.nav-container{
    background-color: #17B169;
}
.box{
    font-size: 1.5rem;
    color:#17B169 ;
    border: 1px solid white;
    border-radius: 5px;
    background-color: whitesmoke;
    
}
.button-box{
    margin-left: 52rem;
}
.logo{
    margin:1.5rem 1rem 1rem 9rem;
    font-weight: 700;
}
.nav-list{
    margin:1.5rem 1rem 1rem 0;
}
.list{
    margin-right: 2.5rem;
    font-weight: 600;
    
}
.anchor{
    text-decoration: none;
    color: #fff;
}
.anchor:hover{
    color: #17B169;
}
.nav-btn{
    
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: #fff;
    visibility: hidden;
    opacity: 0;
    
}
.nav-btn-bar{
    position: absolute;
    top:35%;
    left: 35%;
}
@media screen and (max-width:992px){
    .navbar{
        position: sticky;
        top: 0;
    }
    .logo{
        margin:1.5rem 0rem 1rem 3rem;
    }
    .nav-btn{
        visibility: visible;
        opacity: 1;
    }
    .collapse-bar{
        position: fixed;
        top: 0;
        right:35rem;
        height: 40rem;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
        transition: 1s;
        background-color:#23292e;
        
    }
    .list{
        margin: 1rem;
    }
    .responsive_nav{
        transform: translateY(-100vh);
       
    }

}
