*{
    box-sizing: none;
}
.img-volunter{
    width: 15rem;
    margin-right: 8rem;
}
.volunter{
    margin: 2rem 20rem 5rem 23rem;
}
.box-design{
    font-size: 5rem;
    margin-right: 3rem;
}

@media screen and (max-width:992px){
    .volunter{
        flex-direction: column;
        margin: 0;
    }
    .volunter-content{
        margin: 0;
        padding: 0;
    }
}